.fa {
  padding: 10px;
  font-size: 25px;
}

.fa-color {
  color: #2b3a42 !important;
  transition: 1s;
}

.fa-wrapper{
  position: fixed;
  z-index: 999999;
}
  
.fa:hover {
  opacity: 0.4;
}

.fa-envelope {
  color: #bdd3de;
  transition: 1s;
}

.fa-linkedin-square {
  color: #bdd3de;
  transition: 1s;
}

.fa-github {
  color: #bdd3de;
  transition: 1s;
}

.icon-wrapper {
  padding: 1px;
  position: fixed;
  z-index: 99999999;
  right: 0;
}

.marg-icon{
  margin-top: 95px;
}