@media (max-width: 600px) {

    
    .block {
        height: 100%;
    }

    .description {
        max-width: 60%;
        font-size: 20px;
    }
    .Home-header-text{
        font-size: 40px;
        margin: -50px auto 40px 10px;
    }

    .icon-wrapper {
        right: -10px;
    }
    /* .icon-wrapper {
        width: 30px;
    } */

    .image{
        width: 100%;
    }

    .link-image {
        margin: auto;
        width: 35%;
    }

    .music-description-2 {
        margin: auto auto 30px auto;
    }

    .wrapper-info {
        display: grid;
        grid-template-columns:  100%;
        margin: 1em 40px 0 20px;
    }
}