.block-container {
    /* margin: 65px auto 0; */
}

.block {
    padding: 30px;
    background-color: #bdd3de;
    border-radius: 6px;
    color: #2b3a42;
}

.container-css {
    min-height:1000px;
}

.container-slideshow {
    
    background: transparent;
    z-index:-99999999999;
}

.content-white {
    background-color: #3f5866;
}

.description {
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    font-size: 30px;
    font-family: "Play";
    color: #2b3a42;
}

.fade-in {
    background-color: #3f5866 !important;

    font-size: 21px;
    margin-left: auto;
    margin-right: auto;
    -webkit-animation: fadein .5s; 
       -moz-animation: fadein .5s; 
        -ms-animation: fadein .5s; 
         -o-animation: fadein .5s; 
            animation: fadein .5s;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-moz-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-ms-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-o-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

