.block-container {
    /* margin: 65px auto 0; */
}

.block {
    padding: 30px;
    background-color: #bdd3de;
    border-radius: 6px;
    color: #2b3a42;
}

.container-css {
    min-height:1000px;
}

.container-slideshow {
    
    background: transparent;
    z-index:-99999999999;
}

.content-white {
    background-color: #3f5866;
}

.description {
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    font-size: 30px;
    font-family: "Play";
    color: #2b3a42;
}

.fade-in {
    background-color: #3f5866 !important;

    font-size: 21px;
    margin-left: auto;
    margin-right: auto;
    -webkit-animation: fadein .5s; 
            animation: fadein .5s;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.line-after-description-under-blue {
    margin-top: 275px;
}

.line-after-description-2 {
    width: 100%;
    border-top: 2px solid #2b3a42;
}

.line-after-description-3 {
    width: 100%;
    border-top: 2px solid #2b3a42;
}

.line-for-middle-text {
    border-top: 2px solid #2b3a42;
    width: 80%;
    margin-bottom: 15px;
}

.margin{
    margin-left: auto;
    margin-right: auto;
}

.middle-text {
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    margin-top: 30px;
    margin-bottom: 30px;
    color: #2b3a42;
}

.music-description{
    font-size: 20px;
    margin: auto;
    font-family: "prompt";
    color: #2b3a42;
}

.music-description-2 {
    margin: auto;
    font-family:"prompt";
    font-size: 20px;
    color: #2b3a42;
}

.text-margin {
    margin: auto;
    font-family:"prompt";
    font-size: 20px;
    color: #2b3a42;
}

.wrapper-slideshow-and-contents {
    background-color: #f0f0df;
    margin-top: -275px;
}

.link-image{
    width: 60%;
    margin: auto;
}

.image{
    width: 100%;
}
.fa {
  padding: 10px;
  font-size: 25px;
}

.fa-color {
  color: #2b3a42 !important;
  transition: 1s;
}

.fa-wrapper{
  position: fixed;
  z-index: 999999;
}
  
.fa:hover {
  opacity: 0.4;
}

.fa-envelope {
  color: #bdd3de;
  transition: 1s;
}

.fa-linkedin-square {
  color: #bdd3de;
  transition: 1s;
}

.fa-github {
  color: #bdd3de;
  transition: 1s;
}

.icon-wrapper {
  padding: 1px;
  position: fixed;
  z-index: 99999999;
  right: 0;
}

.marg-icon{
  margin-top: 95px;
}

.buttons-line-container {
    margin: 100px auto 0;
    width: 100vw;
    transition: .5s;
    background-color: #3f5866;
}

.buttons-line-container-smaller {
    margin: 0;
    width: 100vw;
    transition: .5s;
    height: 60px;
    background-color: rgba(255, 255, 255, 0);
}


.Home-content-name {
    transition: 0.5s;
    position: fixed;
    height: 100px;
    margin-top: -10px;
    background-color: #3f5866;
}

/* .Home-content-name-smaller {
    transition: 0.5s;
    height: 0;
} */

.Home-content-name-smaller {
    position: fixed;
    transition: 0.5s;
}


.Home-header{
    background-size: cover;
    background-color: #2b3a42;
    margin-left: auto;
    margin-right: auto;
    transition: 0.5s;
    position: fixed;
}

.Home-header-remove{
    transition: 0.5s;
    background-color: rgba(255, 255, 255, 0);
    position: fixed;
}

.Home-header-text{
    font-size: 80px;
    width: 100vw;
    margin: -40px auto 0 10PX;
    font-family: "voiceinmyhead";
    /* background-color: #3f5866; */
    color:#ff8f00;
}

.Home-header-text-remove {
    color: rgba(255, 255, 255, 0);
}

@font-face {
        font-family: voiceinmyhead;
        src: url("/allanyeznaian/static/media/Future Now.312b980d.ttf") format("opentype");
    }

.Home-nav-buttons-after{
    color: #f0f0df;
    background: transparent;
    cursor: pointer;
    height: 45px;
    width: 100px;
    border: none;
    font-style: italic;
    font-family: "voiceinmyhead";
    font-size: 25px;
    padding: 0 !important;
    position: relative; 
    transition: 1s; 
}

.Home-nav-buttons-after:hover {
    color: #ff8f00;
    transition: 1s;
}

.Home-nav-buttons-after-remove {
    color: rgba(255, 255, 255, 0);
    background: transparent;
    border: none;
}
@media (max-width: 1000px) 
{
    
    .music {
        font-size: 14px;
    }
  }

@media (max-width: 600px) {

    
    .block {
        height: 100%;
    }

    .description {
        max-width: 60%;
        font-size: 20px;
    }
    .Home-header-text{
        font-size: 40px;
        margin: -50px auto 40px 10px;
    }

    .icon-wrapper {
        right: -10px;
    }
    /* .icon-wrapper {
        width: 30px;
    } */

    .image{
        width: 100%;
    }

    .link-image {
        margin: auto;
        width: 35%;
    }

    .music-description-2 {
        margin: auto auto 30px auto;
    }

    .wrapper-info {
        display: grid;
        grid-template-columns:  100%;
        margin: 1em 40px 0 20px;
    }
}
