
.buttons-line-container {
    margin: 100px auto 0;
    width: 100vw;
    transition: .5s;
    background-color: #3f5866;
}

.buttons-line-container-smaller {
    margin: 0;
    width: 100vw;
    transition: .5s;
    height: 60px;
    background-color: rgba(255, 255, 255, 0);
}


.Home-content-name {
    transition: 0.5s;
    position: fixed;
    height: 100px;
    margin-top: -10px;
    background-color: #3f5866;
}

/* .Home-content-name-smaller {
    transition: 0.5s;
    height: 0;
} */

.Home-content-name-smaller {
    position: fixed;
    transition: 0.5s;
}


.Home-header{
    background-size: cover;
    background-color: #2b3a42;
    margin-left: auto;
    margin-right: auto;
    transition: 0.5s;
    position: fixed;
}

.Home-header-remove{
    transition: 0.5s;
    background-color: rgba(255, 255, 255, 0);
    position: fixed;
}

.Home-header-text{
    font-size: 80px;
    width: 100vw;
    margin: -40px auto 0 10PX;
    font-family: "voiceinmyhead";
    /* background-color: #3f5866; */
    color:#ff8f00;
}

.Home-header-text-remove {
    color: rgba(255, 255, 255, 0);
}

@font-face {
        font-family: voiceinmyhead;
        src: url("./fonts/Future\ Now.ttf") format("opentype");
    }

.Home-nav-buttons-after{
    color: #f0f0df;
    background: transparent;
    cursor: pointer;
    height: 45px;
    width: 100px;
    border: none;
    font-style: italic;
    font-family: "voiceinmyhead";
    font-size: 25px;
    padding: 0 !important;
    position: relative; 
    transition: 1s; 
}

.Home-nav-buttons-after:hover {
    color: #ff8f00;
    transition: 1s;
}

.Home-nav-buttons-after-remove {
    color: rgba(255, 255, 255, 0);
    background: transparent;
    border: none;
}